import _ from 'lodash';
import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import { AppBackButton, AppHeading } from '../../components/AppComponents'
import Button from '../../components/Button';
import { InputGroup } from '../../components/form/InputGroup';
import { InputSelect } from '../../components/form/InputSelect';
import { ApiGet, ApiPost, setMessage } from '../../lib/AppHelper';
import { AppStore } from '../../lib/AppStore';
import Main from '../Main';

export class AddEmployee extends Component {

   state = {
      errors: {},
      params: {
         mobile: "",
         name: "",
         email: "",
         reporting_to: ""
      },
      employees: [{
         'label': 'select One',
         'value': ''
      },
      {
         'label': 'Mahendra',
         'value': 1
      },
      {
         'label': 'Sunil Kumar',
         'value': 2
      }]
   }

   MessageRef = React.createRef();

   render() {
      const { errors, params, employees } = this.state;

      return (
         <Main MessageRef={this.MessageRef}>
            <AppBackButton to="/">Back to Dashboard</AppBackButton>
            <div className="w-full px-6 max-w-md md:flex-grow-0 flex-1 flex flex-col justify-between space-y-6">
               <div>
                  <AppHeading>Add Employee</AppHeading>
                  <h4 className="mt-1 text-md mb-6 mt-6">Please enter details</h4>

                  <InputSelect label="Select Team Level" options={employees} attrs={{ value: params.reporting_to, onChange: (e) => this.onChangeHandler(e, 'reporting_to') }} error={errors.reporting_to} onEnter={this.submit} />

                  <InputGroup label="Mobile no" placeholder="0000000000" prefix="+91" attrs={{
                     maxLength: 10, value: params.mobile, onChange: (e) => {
                        this.onChangeHandler({ target: { value: e.target.value.replace(/\D/, '') } }, 'mobile')
                     }, autoFocus: true
                  }} error={errors.mobile} onEnter={this.submit} />

                  <InputGroup error={errors.name} label="Name" placeholder="" attrs={{ value: params.name, onChange: (e) => this.onChangeHandler(e, 'name') }} onEnter={this.submit} />


                  <InputGroup error={errors.email} label="Email" placeholder="" attrs={{ value: params.email, onChange: (e) => this.onChangeHandler(e, 'email') }} onEnter={this.submit} />

                  <Button attrs={{ type: 'button', onClick: this.submit }} size="md" title={"Submit"} />
               </div>
            </div>
         </Main>
      )
   }

   submit = async () => {
      AppStore.dispatch({ type: 'LOADING', loading: true })

      const response = await ApiPost('create/employee', this.state.params)

      if (response.status === "error") {
         AppStore.dispatch({ type: 'LOADING', loading: false })
         if (!_.isEmpty(response.errors)) {
            this.setState({ errors: response.errors })
         } else {
            this.MessageRef.current.show(response.message, "error");
         }
      } else {
         const me = await ApiGet('me');
         if (me.status === 'success') {
            AppStore.dispatch({ type: 'USER_UPDATED', user: me.data })
         }

         setMessage(response.data.message, () => {
            AppStore.dispatch({ type: 'LOADING', loading: false })
            this.props.history.push('/');
         })
      }

   }

   onChangeHandler = (e, key) => {
      const { params } = this.state;
      params[key] = e.target.value;
      this.setState({ params: params })
   }
}

export default withRouter(AddEmployee)
