import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Link, withRouter, useLocation, useParams } from 'react-router-dom';
import { AmountText, AppBackButton, AppStatusLabel } from '../components/AppComponents';
import Button from '../components/Button';
import { ApiGet } from '../lib/AppHelper';
import { AppStore } from '../lib/AppStore';
import Main from './Main'
import moment from 'moment'
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faEnvelopeOpenText, faPhone, faPhoneSquare } from '@fortawesome/free-solid-svg-icons';
import queryString from 'query-string';
import PaginationData from './admin/components/PaginationData';
import { Search } from './Search';


export class Subscriptions extends Component {

  MessageRef = React.createRef();

  state = {
    subscriptions: [],

  }

  render() {
    const { subscriptions } = this.state;
    const { permissions } = this.props;
    // console.log("Helo .........", subscriptions);



    const { loading } = this.props;


    return (
      <Main MessageRef={this.MessageRef}>
        <div className="w-full flex md:flex-row flex-col">
          <div className="w-96"><AppBackButton to="/">Back to Dashboard</AppBackButton></div>
          <div className='w-96 w-full'> <Search></Search></div>
        </div>


        <div className="w-full flex flex-col justify-between space-y-4">
          <div>
            <div className="container my-12 mx-auto px-1 md:px-12">
              <div className="flex flex-wrap -mx-1 lg:-mx-4">
                <PaginationData data={subscriptions} />
              </div>
            </div>
          </div>
        </div>
      </Main>
    )
  }

  componentDidMount() {
    this.getSubscriptions()
  }

  getSubscriptions = async () => {
    AppStore.dispatch({ type: 'LOADING', loading: true })

    // console.log("props=", queryString.parse(this.props.location.search))


    const response = await ApiGet('subscriptions', queryString.parse(this.props.location.search));

    if (response.status === 'success') {
      this.setState({ subscriptions: response.data })

      // console.log("@@@@@@@@@@@", response.data);
    } else {
      this.MessageRef.current.show(response.message)
    }

    AppStore.dispatch({ type: 'LOADING', loading: false })
  }
}

const mapStateToProps = (state) => {
  const { loading } = state
  return { loading: loading }
};

const SubscriptionsConnect = connect(mapStateToProps)(props => {
  return <Subscriptions {...props} />
})

export default withRouter(SubscriptionsConnect)